import mongoose from 'mongoose';
const Schema = mongoose.Schema;

let TrainingRequest = new Schema({
    topic: {
        type: String,
        required: true
    },
    training_type: {
        type: String,
        required: true        
    },
    group_name: {
        type: String,
        required: true
    },
    training_date: {
        type: Date,
        required: true
    },
    prefered_location: {
        type: String,
        required: true
    },
    expected_no_attendees: {
        type: Number
    },
    contact_person: {
        name: {
            type: String,
            required: true
        },
        email: String,
        contact_number: {
            type: String,
            reuired: true
        }
    },
    requested_by: {
        type: Schema.Types.ObjectId,
        required: true
    }
}, {
    timestamps: true
});

module.exports = mongoose.model('TrainingRequest', TrainingRequest);